<template>
  <div>
    <van-tabs v-model="active" color="#fd2744" line-width="26px" line-height="4px">
      <van-tab title="电子普通发票">
        <div class="invoiceBody">
          <van-radio-group v-model="formfm.type">
            <van-radio name="1">个人
              <template #icon="props">
                <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
              </template>
            </van-radio>
            <div class="personInp" v-if="formfm.type === '1'">
              <van-form @submit="onSubmit">
                <van-field
                  v-model="formfm.invoiceTitle"
                  name="发票抬头"
                  label="发票抬头"
                  placeholder="请输入发票抬头"
                  :rules="[{ required: true}]"
                />
                <van-field
                  v-model="formfm.email"
                  type="email"
                  name="邮箱"
                  label="邮箱"
                  placeholder="请输入正常使用的邮箱"
                  :rules="[{ required: true}]"
                />
              </van-form>
          </div>
            <van-radio name="2">单位
              <template #icon="props">
                <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
              </template>
            </van-radio>
            <div class="companyInp" v-if="formfm.type === '2'">
            <van-form @submit="onSubmit">
                <van-field
                  v-model="formfm.invoiceTitle"
                  name="发票抬头"
                  label="发票抬头"
                  placeholder="请输入发票抬头"
                  :rules="[{ required: true}]"
                />
                <van-field
                  v-model="formfm.taxpayerId"
                  type="email"
                  name="纳税人识别号"
                  label="纳税人识别号"
                  placeholder="请按税务登记证填写"
                  :rules="[{ required: true}]"
                />
                <van-field
                  v-model="formfm.email"
                  type="email"
                  name="邮箱"
                  label="邮箱"
                  placeholder="请输入正常使用的邮箱"
                  :rules="[{ required: true}]"
                />
              </van-form>
          </div>
          </van-radio-group>
          <div class="subBtn">确认</div>
          <div class="tishi">
            <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
            <p>申请发票24小时后，即可在您的邮箱中下载电子普通发票。</p>
          </div>
        </div>
      </van-tab>
      <van-tab title="专用发票">
        <div class="purpose">
          <div class="topFont">
            <div class="condition">
              开具专用发票的需订单金额至少累计<span>10000</span>元起
            </div>
            <div class="operation">
              <div class="inLeft">当前订单金额<span>5000</span>元</div>
              <div class="inRight">
                <router-link to="/user/mergeinvoice" class="toMerge"
                  >合并其他订单金额&nbsp;&gt;</router-link
                >
              </div>
            </div>
            <div style="height: 8px"></div>
          </div>

          <div class="entryMsg" v-if="invChoice">
            <van-form >
              <van-field
                v-model="username"
                name="单位名称"
                label="单位名称"
                placeholder="请输入单位名称"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="纳税人识别号"
                label="纳税人识别号"
                placeholder="请输入纳税人识别号"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="注册地址"
                label="注册地址"
                placeholder="注册地址"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="注册电话"
                label="注册电话"
                placeholder="注册电话"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="开户银行"
                label="开户银行"
                placeholder="请输入开户银行"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="银行账户"
                label="银行账户"
                placeholder="请输入银行账户"
                :rules="[{ required: true}]"
              />
              <div class="next" @click="nextStep">下一步</div>
            </van-form>
          </div>
          <div class="entryLast" v-else>
            <div class="entryList">
              <van-field name="radio" label="默认地址">
                <template #input>
                  <van-radio-group v-model="radio" direction="horizontal">
                    <van-radio name="1">同收货地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                    <van-radio name="2">上次邮寄地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                  </van-radio-group>
                </template>
              </van-field>
            </div>
            <van-field
                v-model="password"
                name="姓名"
                label="姓名"
                placeholder="请输入姓名"
                :rules="[{ required: true}]"
              />
              <van-field
                v-model="password"
                name="手机号"
                label="手机号"
                placeholder="请输入手机号"
                :rules="[{ required: true}]"
              />
              <van-field
                readonly
                clickable
                v-model="password"
                name="所在地区"
                label="所在地区"
                placeholder="选择省-市-区-街道"
                :rules="[{ required: true}]"
                @click="showArea = true"
              />
              <van-popup v-model="showArea" position="bottom">
                <van-area
                  :area-list="areaList"
                  @confirm="onConfirm"
                  @cancel="showArea = false"
                />
              </van-popup>
              <van-field
                v-model="password"
                name="详细地址"
                label="详细地址"
                placeholder="请输入详细地址"
                :rules="[{ required: true}]"
              />
            <div class="last">
              <div class="before" @click="backStep">上一步</div>
              <div class="subform">确定</div>
            </div>
          </div>
          <div class="bottomMsg">
            <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
            <p>
              专票在订单签收后8-10个工作日开具并寄出，顺丰寄送，一般会在您订单完成后15个工作日内送达给您（如遇节假日稍有延迟）。请注意查收；增值税专用发票收到后请妥善保存，如退货请一同寄回，如退货专票未能寄回，则需扣除相应的税点。
            </p>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      radio:"1",
      active: 2,
      value:'',
      showArea:false,
      areaList:{},
      invChoice: true,
      formfm: {
        type: "1",
        invoiceTitle: '个人',
        taxpayerId: '',
        email: '',
        orderIds: '', //订单
        invoiceAmount: '' //订单金额
      },
      form: {
        type: 3,
        invoiceTitle: '',
        taxpayerId: '',
        companyAddress: '',
        telphone: '',
        bankName: '',
        bankAccount: '',
        recipient: '',
        recipientPhone: '',
        detailAddress: '',
        corporation: {
          provinceId: '',
          regionId: '',
          townId: '',
          cityId: ''
        },
        cityId: '',
        provinceId: '',
        regionId: '',
        townId: '',
        city: '',
        province: '',
        region: '',
        town: '',
        orderIds: [], //订单
        invoiceAmount: '' //订单金额
      }
    };
  },
  methods: {
    onConfirm(values) {
      this.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      this.showArea = false;
    },
    backStep() {
      this.invChoice = true;
    },
    nextStep() {
      this.invChoice = false;
    },
  },
};
</script>

<style lang="stylus" scoped>
.invoiceBody {
  margin: 10px 4%;
  width: 92%;//345
  height: 200px;
  >>>.van-radio{
    margin 20px 0px 0px 4.34%
  }
  .personInp, .companyInp {
    margin: 8px 0px 28px 11%;
    width: 93.3%;
    >>>.van-cell::after{
      width 88.4%
    }
  }

  .subBtn {
    margin-top: 40px;
    width: 100%;
    height: 48px;
    background: #FF4C4C;
    border-radius: 4px;
    font-size: 17px;
    color: #FFFFFF;
    text-align: center;
    line-height: 48px;
  }

  .tishi {
    margin: 20px 4px;
    width: 338px;
    height: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      width: 322px;
      height: 12px;
      font-size: 10px;
      color: #8D8D8D;
    }
  }
}

.purpose {
  width: 100%;

  .topFont {
    margin: 20px 0px 0px 4%;
    width: 96%;//360
    height: 60px;
    border-bottom: 1px solid #eee;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .condition {
      font-size: 13px;
      color: #666666;

      span {
        color: #111;
      }
    }

    .operation {
      display: flex;
      justify-content: space-between;

      .inLeft {
        font-size: 13px;
        color: #666666;
      }

      .inRight {
        margin-right: 4.1%;

        .toMerge {
          font-size: 12px;
          color: #FF4C4C;
        }
      }
    }
  }
  .entryMsg{
    width: 96%;//360
    margin-left: 4%;
    .next {
      width: 95.8%;//345
      height: 48px;
      background: #FF4C4C;
      border-radius: 4px;
      text-align: center;
      line-height: 48px;
      font-size: 17px;
      color: #FFFFFF;
      margin-top: 34px;
    }
  }
  .entryLast {
    width: 96%;
    margin-left: 4%;
    >>>.van-cell:first-child{
      padding 0px 0px!important
    }
    >>>.van-cell:not(:first-child){
      padding 13px 16px!important
    }
    .entryList {
      width: 94%;
      margin-left 16px
      height: 50px;
      border-bottom: 1px solid #ebedf0;
      display: flex;
      align-items: center;
    }
    .last {
      width: 95.8%;//345
      height: 48px;
      margin-top: 34px;
      display: flex;
      justify-content: space-between;

      .before, .subform {
        width: 43.5%;
        height: 48px;
        background: #FF4C4C;
        border-radius: 4px;
        text-align: center;
        line-height: 48px;
        font-size: 17px;
        color: #FFFFFF;
      }
    }
  }

  .bottomMsg {
    margin: 20px 5.33%;
    width: 90.1%;
    display: flex;
    justify-content: space-between;
    p {
      width: 95%;
      font-size: 10px;
      color: #8D8D8D;
      line-height: 17px;
    }
  }
}
</style>


